import {APP_TITLE, COPYRIGHT} from '../../../conf/app'
import {ROOT_PATH} from '../../../conf/urls'


export default {
    data() {
        return {
            username: '',
            password: '',
            remember: false,
            loading: false
        }
    },

    computed: {
        appName: () => APP_TITLE,
        copyright: () => COPYRIGHT
    },

    methods: {
        login() {
            if (!this.username || !this.password) {
                return this.alert('请输入账号和密码')
            }
            this.loading = true
            this.$store.dispatch('login', {
                data: {
                    username: this.username,
                    password: this.password,
                    remember: this.remember ? 1 : 0
                },
                success: ({success}) => {
                    if (success) {
                        location.href = ROOT_PATH
                    } else {
                        this.alert('登录失败，请检查账号名或密码是否正确')
                    }
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        }
    }
}